import React, {ChangeEvent} from 'react';
import s from './MyPosts.module.css';
import Post from './Post/Post';
import {PostsType} from "../../../redux/state";


type PropsType = {
    posts: Array<PostsType>
    addPostCallback: (postMessage: string) => void
    message: string
    changeNewTextCallback: (newText: string) => void
}

const MyPosts: React.FC<PropsType> = (props) => {
    const postsElement = props.posts.map(p =>
        <Post message={p.message} likesCount={p.likesCount} id={p.id}/>)

    const addPost = () => {
        props.addPostCallback(props.message)
    }
    const newTextChangeHandler = (e: ChangeEvent<HTMLTextAreaElement>) => {props.changeNewTextCallback(e.currentTarget.value)}

    return (
        <div className={s.postsBlock}>
            <h3>My posts</h3>
            <div>
                <div>
                    <textarea value={props.message}
                              onChange={newTextChangeHandler}>
                    </textarea>
                </div>
                <button onClick={addPost}>Add post</button>
            </div>
            <div className={s.posts}>
                <div className={s.post}>{postsElement}</div>
            </div>
        </div>
    )
}
export default MyPosts;